body {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-family: "Roboto";
}

main {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  /* padding-bottom: 50px; */
}

.hidden {
  display: none;
}

.logo-text {
  text-transform: uppercase;
  font-size: 22px;
  font-weight: bold;
  text-shadow: 1px 0 rgba(0,0,0,0.2);
}
.logo-icon {
  text-shadow: 1px 0 rgba(0,0,0,0.2);
}

.page-footer {
  padding-top: 10px;
}

h1, h2, h3, h4, h5 {
  margin: 40px 0;
}

.sidenav-spacer {
  height: 64px;
  text-align: center;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  margin-bottom: 15px;
}
  .sidenav-spacer i {
    line-height: 64px;
    font-size: 38px;
  }

